.card {
  display: flex;
  height: 300px;
  flex-direction: column;
  padding: 32px 12px 16px;
  justify-content: space-between;
  text-align: center;
  gap: 8px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.card h3 {
  height: 2.4em;
  line-height: 1.2em;
  font-weight: 300;
  font-size: 1rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: pre-line;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  padding: 0 10px;
}

.productImage {
  height: 96px;
  max-width: 96px;
  object-fit: scale-down;
}

.productInfo {
  display: flex;
  justify-content: space-between;
}

.upper {
  gap: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lower {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.price {
  font-size: 1.3rem;
}

.card button {
  display: flex;
  gap: 2px;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 8px;
  background-color: #393e46;
  border: none;
  color: #ffffff;
  border-radius: 0;
  transition: background-color 0.1s;
  font-weight: 700;
}

.card button::before {
  content: '';
  background-image: url('../../icons/add.svg');
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: bottom;
  height: 20px;
  width: 20px;
}

.card button::after {
  content: 'Add to cart';
}

.card button.isInCart {
  background-color: #34b234;
}

.card button.isInCart::before {
  background-image: url('../../icons/check.svg');
}

.card button.isInCart::after {
  content: 'Added to cart';
}

.card button.isInCart:hover {
  background-color: #c91d1d;
}

.card button.isInCart:hover::before {
  background-image: url('../../icons/remove.svg');
}

.card button.isInCart:hover::after {
  content: 'Remove from cart';
}
